<template>
  <div class="publish_content">
    <el-card class="box-card" shadow="hover">
      <div slot="header" class="clearfix">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/home">首页</el-breadcrumb-item>
          <el-breadcrumb-item>{{
            $route.query.id ? "修改文章" : "发布文章"
          }}</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-form
        ref="publish-form"
        :model="article"
        :rules="formRules"
        label-width="80px"
      >
        <el-form-item label="标题" prop="title">
          <el-input v-model="article.title"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <el-tiptap
            lang="zh"
            v-model="article.content"
            :extensions="extensions"
          >
          </el-tiptap>
        </el-form-item>
        <el-form-item label="封面">
          <el-radio-group class="my_article" v-model="article.cover.type">
            <el-radio :label="0">无图</el-radio>
            <el-radio :label="1">单图</el-radio>
            <el-radio :label="3">三图</el-radio>
            <el-radio :label="-1">自动</el-radio>
          </el-radio-group>
          <template class="article_img" v-if="article.cover.type > 0">
            <article-img
              v-for="(cover, index) in article.cover.type"
              :key="cover"
              :cover-image="article.cover.images[index]"
              @updata-cover="onUpdateCover(index, $event)"
              class="article_img_item"
            />
          </template>
        </el-form-item>
        <el-form-item label="频道" prop="channel_id">
          <el-select v-model="article.channel_id" placeholder="请选择频道">
            <el-option
              :label="channel.name"
              :value="channel.id"
              v-for="(channel, index) in channels"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onPublish(false)">
            {{ $route.query.id ? "修改" : "发布" }}
          </el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import {
  getArticleChannels,
  addArticle,
  getdateArticle,
  updateArticle
} from '@/api/article'
import { uploadImage } from '@/api/image'
import {
  ElementTiptap,
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Image,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  TodoItem,
  TodoList,
  HorizontalRule,
  Fullscreen,
  Preview,
  CodeBlock,
  TextColor,
  Table,
  TableHeader,
  TableCell,
  TableRow
} from 'element-tiptap'
import ArticleImg from './components/article-img'
import 'element-tiptap/lib/index.css'
export default {
  name: 'PublishIndex',
  components: {
    'el-tiptap': ElementTiptap,
    ArticleImg
  },
  data () {
    return {
      article: {
        tltle: '',
        content: '',
        cover: {
          type: 0,
          images: []
        },
        channel_id: null
      },
      channels: [],
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }),
        new Bold({ bubble: true }),
        new Image({
          uploadRequest (file) {
            const fd = new FormData()
            fd.append('image', file)
            return uploadImage(fd).then((res) => {
              return res.data.data.url
            })
          }
        }),
        new Underline({ bubble: true, menubar: true }),
        new Italic(),
        new Strike(),
        new HorizontalRule(),
        new Fullscreen(),
        new Preview(),
        new CodeBlock(),
        new TextColor(),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
        new TodoItem(),
        new TodoList(),
        new Table(),
        new TableHeader(),
        new TableCell(),
        new TableRow()
      ],
      formRules: {
        title: [
          { required: true, message: '请输入文章标题', trigger: 'blur' },
          { min: 5, max: 30, message: '长度5~30个字符', trigger: 'blur' }
        ],
        content: [
          {
            validator (rule, value, callback) {
              if (value === '<p></p>') {
                // 验证失败
                callback(new Error('请输入文章内容'))
              } else {
                callback()
              }
            }
          },
          { required: true, message: '请输入文章内容', trigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '请选择文章频道', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.loadArticles()
    if (this.$route.query.id) {
      this.uploadArticles()
    }
  },
  methods: {
    loadArticles () {
      getArticleChannels().then((res) => {
        this.channels = res.data.data.channels
      })
    },
    uploadArticles () {
      getdateArticle(this.$route.query.id).then((res) => {
        this.article = res.data.data
      })
    },
    onPublish (draft) {
      this.$refs['publish-form'].validate((valid) => {
        if (!valid) {
          return
        }
        const articleId = this.$route.query.id
        if (articleId) {
          updateArticle(articleId, draft, this.article).then((res) => {
            this.$message({
              message: '修改成功',
              type: 'success'
            })
            this.$router.push('/home')
          })
        } else {
          addArticle(draft, this.article).then((res) => {
            this.$message({
              message: '发布成功',
              type: 'success'
            })
            this.$router.push('/home')
          })
        }
      })
    },
    onUpdateCover (index, url) {
      this.article.cover.images[index] = url
    }
  }
}
</script>

<style scoped lang="less">
.my_article {
  display: block;
  margin-top: 12px;
}
.article_img {
  margin-top: 20px;
}
.article_img_item {
  float: left;
}
</style>
